/**
 * Client build(for Domain)
 * const baseUrl =  "/",
 *
 * NIS test build
 * const baseUrl = "/biz/";
 */

/**Please enable below two lines while generating business build */
// const baseUrl = "/biz/";
// let routes = userRoutes;

/**Please enable below two lines while generating admin build */
const baseUrl = "/mng/";
let routes = adminRoutes;

const router = new Router({
  /* toggle below two lines if  you want to remove # symbol and redired admin route to admin login */
  mode: "history",
  base: baseUrl,
  linkActiveClass: "open active",
  scrollBehavior: () => ({ y: 0 }),
  routes: routes,
});

// This callback runs before every route change, including on page load.
router.beforeEach((to, from, next) => {
  /** auth guards */
  if (to.meta.auth == true) {
    if (!localStorage.getItem("token")) {
      next("/login");
    } else {
      if (to.meta.user == true && localStorage.getItem("role") == "ADMIN") {
        next("/login"); // go to 'admin/login';
      }
      if (to.meta.admin == true && localStorage.getItem("role") == "USER") {
        next("/login"); // go to 'user/login';
      }
    }
  }
  if (to.meta.access_key) {
    const rule = checkAccessControl(to.meta.access_key);
    if (!rule) {
      return next({ name: "admin_login" });
    }
  }
  // This goes through the matched routes from last to first, finding the closest route with a title.
  // eg. if we have /some/deep/nested/route and /some, /deep, and /nested have titles, nested's will be chosen.
  const nearestWithTitle = to.matched
    .slice()
    .reverse()
    .find((r) => r.meta && r.meta.title);

  // Find the nearest route element with meta tags.
  const nearestWithMeta = to.matched
    .slice()
    .reverse()
    .find((r) => r.meta && r.meta.metaTags);

  // If a route with a title was found, set the document (page) title to that value.
  if (nearestWithTitle) document.title = nearestWithTitle.meta.title;

  // Remove any stale meta tags from the document using the key attribute we set below.
  let removedElements = Array.from(
    document.querySelectorAll("[data-vue-router-controlled]")
  ).map((el) => el.parentNode.removeChild(el));
  logValuesInSession("removedElements", removedElements);
  // Skip rendering meta tags if there are none.
  if (!nearestWithMeta) return next();

  // Turn the meta tag definitions into actual elements in the head.
  nearestWithMeta.meta.metaTags
    .map((tagDef) => {
      const tag = document.createElement("meta");

      Object.keys(tagDef).forEach((key) => {
        tag.setAttribute(key, tagDef[key]);
      });

      // We use this to track which meta tags we create, so we don't interfere with other ones.
      tag.setAttribute("data-vue-router-controlled", "");

      return tag;
    })
    // Add the meta tags to the document head.
    .forEach((tag) => document.head.appendChild(tag));

  next();
});
export default router;
